/* Global */
body {
  background-image: url(/static/media/darkmode.2f3ac40a.png);
  height: 100vh; }

/* Header */
#header-nav {
  background-color: #53aa7d;
  background-image: linear-gradient(45deg, #53aa7d 0%, #00c958 100%);
  box-shadow: 0 0px 2px rgba(0, 0, 0, 0.75); }

#nav-title {
  font-family: 'Pacifico', cursive;
  font-size: 2rem; }

/* Main */
#map {
  min-height: 800px;
  width: 100%;
  border-radius: 25px; }

/* NewsFeed */
/* NewsCard */
.news-card, #map {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  border: none; }

.card-container {
  max-height: 800px;
  border-radius: 25px; }

.news-card, .news-card:hover, .news-card:focus, .news-card:active {
  text-decoration: none;
  color: inherit;
  transition: background-color 300ms ease-out; }

.news-card:hover {
  background-color: rgba(130, 101, 235, 0.1);
  transition: all 50ms ease-out; }

.article-thumbnail {
  object-fit: cover;
  max-width: 100px; }

.article-title {
  font-size: 1rem;
  display: inline; }

.save-button {
  padding: 0px 7px; }

.article-description, .article-publisher, .article-published-date {
  font-size: .75rem;
  line-height: 1rem; }

/* Media Queries */
@media only screen and (max-width: 425px) {
  .article-description {
    display: none; }
  .article-thumbnail {
    height: 70px; }
  #map {
    height: 580px; } }


